<template>
  <div class="pages">
    <div class="tabMenu">
      <div class="boxMain">
        <a @click="toGdb()">机枪池</a>
      </div>
    </div>
    <div class="banner">
      <h2>
        <span
          v-if="symbol2&&poolType == 'yii'"
        >质押G-{{symbol1.toUpperCase()}}/{{symbol2.toUpperCase()}} LP获取GDB</span>
        <span
          v-if="symbol2&&poolType != 'yii'"
        >质押{{symbol1.toUpperCase()}}/{{symbol2.toUpperCase()}} LP获取GDB</span>

        <span v-if="!symbol2">质押{{symbol1.toUpperCase()}}获取GDB</span>
      </h2>
      <div
        class="pool-tips"
        v-if="poolType=='yii'"
      >在机枪池存入{{symbol1.toUpperCase()}}/{{symbol2.toUpperCase()}} LP，既可获得G-{{symbol1.toUpperCase()}}/{{symbol2.toUpperCase()}} LP</div>
      <div v-if="!isOver" class="pool-tips">{{tokenTips}}</div>
      <div v-if="isOver" class="pool-tips">挖矿已结束,请赎回自己的Token</div>
    </div>

    <div class="earn">
      <div class="tabMain">
        <ul>
          <li style="display: flex;align-items: center;justify-content: center;"  v-if="!(tokenName=='GDTUSDTLP'||pool=='GDTHUSDLPV2')">
            <div class="boxMain hot">
              <div class="iconBox">
                <div class="icon">
                  <img src="https://cdn.jsdelivr.net/gh/gamedaoio/HgbApp/static/tokens/gdb.png" alt />
                </div>
              </div>
              <!-- <div class="currency">总量</div>
              <div class="money fontStyle">{{totalReward|filterTokenNumber(18,8)}}</div>-->

              <div class="currency">预计获得的GDB</div>
              <div class="money fontStyle">{{myPoolUnclaimed|filterTokenNumber(18,8)}}</div>

              <div class="pool-button">
                <el-button type="primary" round @click="claimgdb">领取GDB</el-button>
                <div class="pool-tips"></div>
              </div>
            </div>
          </li>
          <li>
            <div class="boxMain hot">
              <div class="iconBox">
                <div class="icon">
                  <img
                    :src="'https://cdn.jsdelivr.net/gh/gamedaoio/HgbApp/static/tokens/'+symbol1+'.png'"
                    alt
                  />
                </div>
                <div class="icon" v-if="symbol2">
                  <img
                    :src="'https://cdn.jsdelivr.net/gh/gamedaoio/HgbApp/static/tokens/'+symbol2+'.png'"
                    alt
                  />
                </div>
              </div>
              <div>我的</div>
              <div class="money fontStyle">
                {{myPoolAmount|filterTokenNumber(this.tokenDecimals,8)}} {{symbol}}
                <span>({{myPoolPercent}}%)</span>
              </div>
              <el-divider></el-divider>

              <div>全网</div>
              <div
                class="money fontStyle"
              >{{totalPoolAmount|filterTokenNumber(this.tokenDecimals,8)}}</div>
              <!-- <div
                v-if="symbol2"
                style="margin-bottom: 5px;"
              >{{gdtAmount|filterNumber(2)}} {{symbol1.toUpperCase()}}/{{husdAmount|filterNumber(2)}} {{symbol2.toUpperCase()}}</div>-->
              <div style="color: #02bbf0;font-size: 18px;font-weight: 700;" v-if="!isOver">
                APY:
                <span>{{apy}}%</span>
              </div>
              <!-- <div> -->
              <div class="pool-action">
                <el-button type="warning" round @click="showWithdrawDialog">赎回</el-button>
                <el-button type="primary" round @click="showStakeDialog" v-if="!(tokenName=='GDTUSDTLP'||pool=='GDTHUSDLPV2')">质押</el-button>
              </div>
              <div class="pool-action pool-action-button" v-if="!isOver">
                <el-button type="success" v-if="pool!='GMDXUSDTLP'" round @click="toMdex">去添加流通性</el-button>
                <el-button type="success" v-if="pool=='GMDXUSDTLP'" round @click="toVault">去机枪池</el-button>
              </div>
            </div>
          </li>
        </ul>
      </div>
      <!-- <div class="tips">为指定的Token提供流动性，合约将自动检测您的做市凭证并发放GDB奖励！</div> -->
    </div>

    <el-dialog title="质押挖矿" :visible.sync="stakeDialogVisiable" width="100%" center>
      <div class="stake-dialog">
        <el-input v-model="inputAmount" placeholder="请输入质押金额">
          <span slot="append" size="mini" @click="setMaxInputAmount()">最大</span>
        </el-input>

        <el-button type="primary" round @click="stake">确认质押</el-button>
        <div>我的余额:{{myCurTokenBalance|filterTokenNumber(this.tokenDecimals,8)}}</div>
      </div>
    </el-dialog>

    <el-dialog title="赎回" :visible.sync="withdrawDialogVisiable" width="100%" center>
      <div class="unstake-dialog">
        <div class="unstake-box">
          <div class="unstake-header">
            <p>数量</p>
            <p>
              抵押数量：
              <span>{{myPoolAmount|filterTokenNumber(this.tokenDecimals,8)}}</span>
            </p>
          </div>
          <div class="unstake-divider">
            <el-input class="nft-input-item" v-model="unstakeAmount" placeholder="请输入质押量"></el-input>
          </div>
          <div class="unstake-mark">
            <el-button type="default" @click="setUnstakeAmount(25)">25%</el-button>
            <el-button type="default" @click="setUnstakeAmount(50)">50%</el-button>
            <el-button type="default" @click="setUnstakeAmount(75)">75%</el-button>
            <el-button type="default" @click="setUnstakeAmount(100)">100%</el-button>
          </div>
        </div>

        <div class="assets" v-if="false">
          <p>赎回资产</p>
          <div class="assetsFrom">
            <div class="alignLeft">
              <div class="el-image logo">
                <img src class="el-image__inner" />
                <!---->
              </div>
              <span>GDB</span>
            </div>
            <div class="alignRight">GDB</div>
          </div>
          <div class="assetsTo">
            <div class="alignLeft">
              <div class="el-image logo">
                <img src class="el-image__inner" />
                <!---->
              </div>
              <span></span>
            </div>
            <div class="alignRight"></div>
          </div>
        </div>
        <div class="unstake-button">
          <el-button type="primary" round @click="withdraw">确认赎回</el-button>
        </div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import store from "@/store/index";
var path = store.state.contractPath;

import Util from "../../utils/util";

const erc20AbiJosn = require("../../contracts/erc20.json");
const gdbAbiJsons = require("../../contracts/gdbdeployments." + path + ".json");
const gdbPoolsJson = require("../../contracts/gdbpools." + path + ".json");
const tokensJson = require("../../contracts/tokens." + path + ".json");

import Swap from "../../utils/swap";

export default {
  data() {
    return {
      pool: "",
      symbol1: "",
      symbol2: "",
      txs: {},

      tokenTips: "",

      tokenDecimals: 18,

      poolContract: null,
      tokenContract: null,
      gdtContract: null,
      account: null,

      loading: null,
      symbol: "",

      totalPoolAmount: 0,
      totalReward: 0,
      perReward: 0,

      gdtAmount: 0,
      husdAmount: 0,

      myPoolAmount: 0,
      myPoolPercent: "0.00",
      myPoolUnclaimed: 0,
      myPoolUnclaimed1: 0,
      myPoolUnclaimedTime: 0,

      myCurTokenBalance: 0,
      myCurTokenAllowance: 0,
      apy: "--",

      inputAmount: "",
      stakeAmount: "",

      tokenAddress: "",
      poolAddress: "",
      poolAbi: "",

      stakeDialogVisiable: false,
      withdrawDialogVisiable: false,
      unstakeAmount: 0
    };
  },

  computed: {
    web3() {
      return this.$store.state.web3;
    }
  },
  watch: {
    web3(val1, val2) {
      this.init();
    }
  },
  beforeMount() {
    this.pool = this.$route.params.pool;
    var json = gdbPoolsJson["mines"][this.pool];
    this.tokenAddress = json.tokenAddress;
    var tokenName = json.tokenName;
    this.tokenName = tokenName;
    this.poolType = json.type;
    this.tokenDecimals = json.decimals;
    this.tokenTips = json["tips"];

    this.poolAddress = json.poolAddress;
    this.poolAbi = gdbAbiJsons[json.poolName]["abi"];
    if (this.pool.indexOf("LP") >= 0) {
      this.symbol1 = json.tokenName1.toLowerCase();
      this.symbol2 = json.tokenName2.toLowerCase();
    } else {
      this.symbol1 = tokenName.toLowerCase();
    }
    // this.init();
  },
  mounted() {
    this.hideLoading();
    this.init();

    clearInterval(this.updateInterval);

    this.updateInterval = setInterval(
      function() {
        this.update();
      }.bind(this),
      100
    );
  },
  beforeDestroy() {
    this.hideLoading();
    clearInterval(this.updateInterval);
  },
  methods: {
    toMdex() {
      var token1 = tokensJson[this.symbol1.toUpperCase()]["address"];
      var token2 = tokensJson[this.symbol2.toUpperCase()]["address"];

      if (this.tokenName == "GDTUSDTLP") {
        window.open("https://dogeswap.com/#/add/" + token1 + "/" + token2);
      } else {
        window.open("https://ht.mdex.com/#/add/" + token1 + "/" + token2);
      }
    },
    toVault() {
      this.$router.push({ name: "gdbVaultScene", params: { pool: this.pool } });
    },
    init() {
      if (!this.web3 || !this.web3.web3Instance) {
        return;
      }
      var web3 = this.web3.web3Instance();

      console.log(this.tokenAddress, "//////");
      this.tokenContract = new web3.eth.Contract(
        erc20AbiJosn,
        this.tokenAddress,
        {
          from: this.web3.coinbase,
          gasPrice: "2000000000"
        }
      );

      // this.gdbContract = new web3.eth.Contract(
      //   erc20AbiJosn,
      //   tokensJson["GDB"]["address"],
      //   {
      //     from: this.web3.coinbase,
      //     gasPrice: "2000000000"
      //   }
      // );

      // console.log(item);
      if (this.poolAddress) {
        this.poolContract = new web3.eth.Contract(
          this.poolAbi,
          this.poolAddress,
          {
            from: this.web3.coinbase,
            gasPrice: "2000000000"
            // gas: 7702600
          }
        );
      }

      this.getInfo();
    },
    calApy() {
      var that = this;
      // console.log(this.tokenName)
      Swap.getTokenPrice("GDB", function(price, amount1, amount2) {
        that.gdbPrice = price;
        if (that.tokenName.indexOf("LP") > 0) {
          that.husdAmount = amount1;
          // that.gdtAmount = amount2;

          if (that.totalPoolAmount == 0) {
            return;
          }
          if (
            that.tokenName == "GDTHUSDLP" ||
            that.tokenName == "GDTHUSDLPV2"
          ) {
            Swap.getTokenPrice("GDT", function(price, amount1, amount2) {
              var lp =
                Math.pow(
                  parseFloat(amount1) * parseFloat(amount2) * 10 ** 10,
                  0.5
                ) *
                10 ** 8;

              var totalPoolAmount =
                (parseFloat(that.totalPoolAmount) / lp) * parseFloat(amount1); //totalPoolAmount / 10 ** 13;

              that.apy = Number(
                ((that.perReward / 10 ** 18) *
                  86400 *
                  365 *
                  100 *
                  that.gdbPrice) /
                  totalPoolAmount /
                  2
              ).toFixed(2);
            });
          } else if (that.tokenName == "GDBHUSDLP") {
            var lp =
              Math.pow(
                parseFloat(amount1) * parseFloat(amount2) * 10 ** 10,
                0.5
              ) *
              10 ** 8;

            var totalPoolAmount =
              (parseFloat(that.totalPoolAmount) / lp) * parseFloat(amount1); //totalPoolAmount / 10 ** 13;

            that.apy = Number(
              ((that.perReward / 10 ** 18) *
                86400 *
                365 *
                100 *
                that.gdbPrice) /
                totalPoolAmount /
                2
            ).toFixed(2);
          } else if (that.tokenName == "GMDXUSDTLP") {
            Swap.getTokenLp("MDX", "USDT", function(price, amount1, amount2) {
              var lp =
                Math.pow(parseFloat(amount1) * parseFloat(amount2), 0.5) *
                10 ** 18;

              var totalPoolAmount =
                (parseFloat(that.totalPoolAmount) / lp) * parseFloat(amount2); //totalPoolAmount / 10 ** 13;

              that.apy = Number(
                ((that.perReward / 10 ** 18) *
                  86400 *
                  365 *
                  100 *
                  that.gdbPrice) /
                  totalPoolAmount /
                  2
              ).toFixed(2);
            });
          } else if (that.tokenName == "GDTUSDTLP") {
            Swap.getTokenPrice("GDT", function(price, amount1, amount2) {
              var lp =
                Math.pow(
                  parseFloat(amount1) * parseFloat(amount2) * 10 ** 10,
                  0.5
                ) *
                10 ** 8;

              // var totalPoolAmount =
              //   (parseFloat(that.totalPoolAmount) / lp) * parseFloat(amount1); //totalPoolAmount / 10 ** 13;

              var totalPoolAmount = Math.pow(
                (that.totalPoolAmount / 10 ** 18) ** 2 * price,
                0.5
              );

              that.apy = Number(
                ((that.perReward / 10 ** 18) *
                  86400 *
                  365 *
                  100 *
                  that.gdbPrice) /
                  totalPoolAmount /
                  2
              ).toFixed(2);
            });
          }
          return;
        } else {
          Swap.getTokenPrice(that.tokenName, function(price) {
            that.tokenPrice = price;
            var totalPoolAmount = 1;
            if (that.totalPoolAmount == 0) {
              return;
            }
            totalPoolAmount = that.totalPoolAmount / 10 ** that.tokenDecimals;

            that.apy = Number(
              ((((that.perReward / 10 ** 18) * 86400 * 365 * 100) /
                that.tokenPrice) *
                4 *
                that.gdbPrice) /
                totalPoolAmount
            ).toFixed(2);
          });
        }
      });
    },
    toGdb(type) {
      this.$router.push({ name: "gdbScene" });
    },
    update() {
      if (this.totalPoolAmount == 0) {
        return;
      }
      if (this.endTime < parseInt(new Date() / 1000)) {
        this.isOver = true;
      }
      var curtime = parseInt(new Date() / 1) / 1000;
      var t = 0;
      if (this.myPoolUnclaimedTime > this.endTime) {
        return;
      } else if (curtime > this.endTime) {
        t = this.endTime - this.myPoolUnclaimedTime;
      } else {
        t = parseInt(new Date() / 1) / 1000 - this.myPoolUnclaimedTime;
      }

      var amount =
        (t * this.perReward * this.myPoolAmount) / this.totalPoolAmount;
      amount = amount + "";
      amount = amount.split(".")[0];

      this.myPoolUnclaimed = Util.add(this.myPoolUnclaimed1, amount).toString();
    },

    getInfo() {
      this.getMineInfo();
      this.getMyCurTokenBalance();
      this.getMyCurTokenAllowance();
    },

    getMineInfo() {
      var symbol = this.symbol1.toUpperCase();

      this.poolContract.methods
        .getMineInfo()
        .call({ from: this.$store.state.contractFrom })
        .then(res => {
          // (startTime, totalDuration, totalReward,
          //   totalAmount, totalAdjust,
          //   mortgageAmounts[owner], mortgageAdjusts[owner]);
          this.startTime = res[0];
          this.endTime = parseInt(res[0]) + parseInt(res[1]);
          this.isOver = false;
          if(this.pool == "GDTHUSDLPV2" || this.pool == "GDTUSDTLP"){
            this.isOver = true
          }
          if (this.endTime < parseInt(new Date() / 1000)) {
            this.isOver = true;
          }
          this.totalReward = res[2];
          this.totalPoolAmount = res[3];
          this.perReward = res[5];
          console.log(this.perReward, ".................perReward");
          this.calApy();
          this.getMyMineInfo();
        });
    },
    getMyMineInfo() {
      this.poolContract.methods
        .getMyMineInfo(this.web3.coinbase)
        .call({ from: this.$store.state.contractFrom })
        .then(res => {
          this.myPoolAmount = res[0];
          this.myPoolUnclaimed = res[1];
          this.myPoolUnclaimed1 = res[1];
          this.myPoolUnclaimedTime = res[3];

          if (this.isOver) {
            if (this.myPoolUnclaimed < 0) {
              this.myPoolUnclaimed = 0;
              this.myPoolUnclaimed1 = 0;
            }
          }
          if (parseFloat(this.totalPoolAmount) > 0) {
            this.myPoolPercent = Number(
              (parseFloat(this.myPoolAmount) /
                parseFloat(this.totalPoolAmount)) *
                100
            ).toFixed(2);
          }
        });
    },

    getMyCurTokenBalance() {
      if (this.tokenName.toUpperCase() == "HT") {
        this.web3
          .web3Instance()
          .eth.getBalance(this.web3.coinbase)
          .then(res => {
            this.myCurTokenBalance = res;
          });
      } else {
        // console.log("getMyCurTokenBalance", this.tokenContract);
        this.tokenContract.methods
          .balanceOf(this.web3.coinbase)
          .call({ from: this.$store.state.contractFrom })
          .then(res => {
            this.myCurTokenBalance = res;
          });
      }
    },
    getMyCurTokenAllowance() {
      if (this.tokenName.toUpperCase() == "HT") {
        return;
      }
      this.tokenContract.methods
        .allowance(this.web3.coinbase, this.poolAddress)
        .call({ from: this.$store.state.contractFrom })
        .then(res => {
          this.myCurTokenAllowance = res;
        });
    },
    showStakeDialog() {
      var symbol = this.symbol1.toUpperCase();
      var curtime = parseInt(new Date() / 1000);
      if (curtime < 1614167700 && (symbol == "BAGS" || symbol == "HPT")) {
        this.$message({
          message: "2月24号 20:00开启",
          type: "warning",
          center: true
        });
        return;
      }
      if (this.endTime < parseInt(new Date() / 1000)) {
        this.isOver = true;
      }
      if (this.isOver) {
        this.$message({
          message: "活动已结束无法再质押",
          type: "warning",
          center: true
        });
        return;
      }
      this.stakeDialogVisiable = true;
      this.getMyCurTokenBalance();
      this.getMyCurTokenAllowance();
    },
    setMaxInputAmount() {
      this.inputAmount = Util.toNumber(
        this.myCurTokenBalance,
        this.tokenDecimals,
        8
      );
    },

    approval(amount) {
      var that = this;
      this.tokenContract.methods
        .approve(this.poolAddress, amount)
        .send()
        .on("transactionHash", function(hash) {
          console.log(hash);
          that.showLoading("授权上链中");
        })
        .on("receipt", function(receipt) {
          console.log(receipt);
          if (that.txs[receipt.transactionHash]) {
            return;
          }
          that.txs[receipt.transactionHash] = receipt.transactionHash;
          that.showLoading("授权已确认,质押中");

          that._stakeConfirm();
        })
        .on("confirmation", function(confirmationNumber, receipt) {
          console.log(confirmationNumber);
        })
        .on("error", function(err) {
          console.log(err);
          that.hideLoading();
          that.$message({
            message: "授权失败" + err.message,
            type: "error",
            center: true
          });
        });
    },

    approvalgdt() {
      var that = this;
      var amount = Util.mul(this.nftTotalAmount * 100, 10 ** 18).toString();

      // var amount = Util.mul(this.stakeAmount, 100).toString();

      this.gdtContract.methods
        .approve(this.poolAddress, amount)
        .send()
        .on("transactionHash", function(hash) {
          console.log(hash);
          that.showLoading("授权上链中");
        })
        .on("receipt", function(receipt) {
          console.log(receipt);
          if (that.txs[receipt.transactionHash]) {
            return;
          }
          that.txs[receipt.transactionHash] = receipt.transactionHash;
          that.hideLoading();
          that.$message({
            message: "授权成功",
            type: "success",
            center: true
          });
        })
        .on("confirmation", function(confirmationNumber, receipt) {
          console.log(confirmationNumber);
        })
        .on("error", function(err) {
          console.log(err);
          that.hideLoading();
          that.$message({
            message: "授权失败" + err.message,
            type: "error",
            center: true
          });
        });
    },
    stake() {
      console.log(this.tokenName, this.startTime);

      var curtime = parseInt(new Date() / 1000);
      if (this.startTime > curtime) {
        this.$message({
          message: this.tokenTips,
          type: "warning",
          center: true
        });
        return;
      }
      if (this.endTime < curtime) {
        this.isOver = true;
      }
      if (this.isOver) {
        this.$message({
          message: "活动已结束无法再质押",
          type: "warning",
          center: true
        });
        return;
      }
      if (this.inputAmount <= 0) {
        this.$message({
          message: "输入金额不正确",
          type: "error",
          center: true
        });
        return;
      }
      var curtime = parseInt(new Date() / 1000);

      var number = this.tokenDecimals >= 8 ? 8 : this.tokenDecimals;
      var amount = parseInt(parseFloat(this.inputAmount) * 10 ** number);
      amount = Util.mul(amount, 10 ** (this.tokenDecimals - number)).toString();
      this.stakeAmount = amount;

      if (Util.compareTo(this.stakeAmount, this.myCurTokenBalance) > 0) {
        console.log(this.stakeAmount, this.myCurTokenBalance);
        this.$message({
          message: "余额不足",
          type: "error",
          center: true
        });
        return;
      }

      if (this.tokenName.toUpperCase() == "HT") {
        this._stakeHT();
        return;
      } else {
        if (Util.compareTo(this.myCurTokenAllowance, this.stakeAmount) < 0) {
          var amount = Util.mul(this.stakeAmount, 100).toString();
          this.approval(amount);
        } else {
          this.showLoading("质押中");
          this._stakeConfirm();
        }
      }
      this.showLoading("授权中");
    },
    _stakeConfirm() {
      console.log("_stakeConfirm");

      var that = this;
      this.poolContract.methods
        .mortgageAdd(this.stakeAmount)
        .send()
        .on("transactionHash", function(hash) {
          console.log(hash);
          that.showLoading("质押上链中");
        })
        .on("receipt", function(receipt) {
          console.log(receipt);
          if (that.txs[receipt.transactionHash]) {
            return;
          }
          that.txs[receipt.transactionHash] = receipt.transactionHash;
          that.stakeDialogVisiable = false;
          that.hideLoading();
          that.$message({
            message: "质押成功",
            type: "success",
            center: true
          });
          that.getInfo();
        })
        .on("confirmation", function(confirmationNumber, receipt) {
          console.log(confirmationNumber);
        })
        .on("error", function(err) {
          that.hideLoading();
          console.log(err);
          that.$message({
            message: "质押失败" + err.message,
            type: "error",
            center: true
          });
        });
      // return;
    },
    _stakeHT() {
      console.log("_stakeHT");

      var that = this;
      this.showLoading("质押中");

      console.log(this.stakeAmount, "hhhhhhhhhhhh");
      this.poolContract.methods
        .mortgageAdd(this.stakeAmount)
        .send({ value: this.stakeAmount })
        .on("transactionHash", function(hash) {
          console.log(hash);
          that.showLoading("质押上链中");
        })
        .on("receipt", function(receipt) {
          console.log(receipt);
          if (that.txs[receipt.transactionHash]) {
            return;
          }
          that.txs[receipt.transactionHash] = receipt.transactionHash;
          that.stakeDialogVisiable = false;
          that.hideLoading();
          that.$message({
            message: "质押成功",
            type: "success",
            center: true
          });
          that.getInfo();
        })
        .on("confirmation", function(confirmationNumber, receipt) {
          console.log(confirmationNumber);
        })
        .on("error", function(err) {
          that.hideLoading();
          console.log(err);
          that.$message({
            message: "质押失败" + err.message,
            type: "error",
            center: true
          });
        });
      // return;
    },
    showWithdrawDialog() {
      this.withdrawDialogVisiable = true;
    },
    setUnstakeAmount(factor) {
      var amount = Util.mul(this.myPoolAmount, factor);
      this.unstakeAmount = Util.toNumber(
        Util.div(amount, 100),
        this.tokenDecimals,
        8
      );
    },
    withdraw() {
      // return;
      this.showLoading("赎回中");
      console.log(this.myAmount);
      var amount = Number(this.unstakeAmount).toFixed(8);
      amount = parseInt(parseFloat(amount) * 10 ** 8);
      amount = Util.mul(amount, 10 ** this.tokenDecimals);
      amount = Util.div(amount, 10 ** 8);
      var that = this;
      console.log(amount.toString());

      this.poolContract.methods
        .mortgageSub(amount.toString())
        .send()
        .on("transactionHash", function(hash) {
          console.log(hash);
          that.showLoading("赎回上链确认中");
        })
        .on("receipt", function(receipt) {
          console.log(receipt);
          if (that.txs[receipt.transactionHash]) {
            return;
          }
          that.txs[receipt.transactionHash] = receipt.transactionHash;
          that.hideLoading();
          that.withdrawDialogVisiable = false;
          that.getInfo();
          that.$message({
            message: "赎回成功",
            type: "success",
            center: true
          });
        })
        .on("confirmation", function(confirmationNumber, receipt) {
          console.log(confirmationNumber);
        })
        .on("error", function(error) {
          that.hideLoading();
          console.log(error);
          that.$message({
            message: "赎回失败" + error.message,
            type: "error",
            center: true
          });
        });
    },

    claimgdb() {
      this.showLoading("领取GDB中");
      var that = this;
      this.poolContract.methods
        .claim()
        .send()
        .on("transactionHash", function(hash) {
          console.log(hash);
          that.showLoading("领取GDB上链确认中");
        })
        .on("receipt", function(receipt) {
          console.log("receipt", receipt);
          if (that.txs[receipt.transactionHash]) {
            return;
          }
          that.txs[receipt.transactionHash] = receipt.transactionHash;
          that.hideLoading();
          that.getInfo();
          that.$message({
            message: "领取GDB成功",
            type: "success",
            center: true
          });
        })
        .on("confirmation", function(confirmationNumber, receipt) {
          console.log(confirmationNumber);
        })
        .on("error", function(error) {
          that.hideLoading();
          console.log(error);
          that.$message({
            message: "领取GDB失败" + error.message,
            type: "error",
            center: true
          });
        });
    },
    hideLoading() {
      if (this.loading) {
        this.loading.close();
        this.loading = null;
        return;
      }
    },
    showLoading(text) {
      if (this.loading) {
        this.loading.text = text;
        return;
      }

      this.loading = this.$loading({
        lock: true,
        text: text,
        spinner: "el-icon-loading",
        background: "rgba(0, 0, 0, 0.7)"
      });
    }
  },

  filters: {
    filterTokenNumber(value, decimals, num) {
      return Util.toNumber(value, decimals, num);
    },
    filterNumber(value, num) {
      return Number(value).toFixed(num);
    }
  }
};
</script>

<style scoped>
.pages {
  margin-bottom: 70px;
  max-width: 1200px;
  margin: 0 auto;
  zoom: 1;
  padding: 0 15px;
}

.banner {
  height: 150px;
  width: 100%;

  display: -ms-flexbox;
  display: flex;

  justify-content: center;

  align-content: center;

  flex-direction: column;
  text-align: center;
}

.banner h2 {
  font-size: 28px;
  color: #fff;
  line-height: 50px;
}
.banner .pool-tips {
  font-size: 16px;
  margin-bottom: 20px;
  color: #fff;
  font-weight: 200;
}
.tabMenu .boxMain,
.tabMenu {
  display: flex;

  justify-content: center;
}

.tabMenu {
  margin-bottom: 30px;
}

.tabMenu .boxMain {
  border: 1px solid #32c1ea;
  border-radius: 21px;
}

.tabMenu .boxMain a.select {
  background: #32c1ea;
  color: #fff;
}
.tabMenu .boxMain a {
  height: 42px;
  border-radius: 21px;
  display: block;
  line-height: 42px;
  font-size: 17px;
  padding: 0 30px;
  color: #fff;
  width: 100%;
  text-align: center;
  white-space: nowrap;
}

.tabMain ul {
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  justify-content: space-between;
}
.tabMain ul li {
  position: relative;
}
.earn ul li {
  width: 40%;
  background: #fff;

  box-shadow: 0 3px 68px 0 rgba(0, 159, 190, 0.08);
  border-radius: 40px;
  padding: 20px 40px;
  flex-direction: column;
  text-align: center;
}
.earn ul li .iconBox {
  margin-bottom: 30px;
  width: 100%;
  display: flex;
  justify-content: center;
  text-align: center;
}
.earn ul li .icon {
  width: 100px;
  height: 100px;
  background: #bcf0f9;
  border: 4px solid #91d3dd;
  border-radius: 50%;
  /* justify-content: center; */
  /* margin: 0 auto; */
}

.earn ul li .icon img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.earn ul li .money {
  font-size: 32px;
  font-weight: 700;
}

.earn ul li .money-desc {
  font-size: 20px;
  font-weight: 300;
}
.earn ul li .money-desc1 {
  font-size: 16px;
  font-weight: 300;
}
.earn ul li .money span {
  display: block;
  font-size: 14px;
  font-weight: 300;
}

.earn /deep/ .el-divider--horizontal {
  margin: 12px 0;
}
.earn ul li .currency {
  color: #353535;
  font-size: 22px;
  line-height: 30px;
  font-weight: 700;
}
.earn ul li /deep/ .el-button {
  width: 170px;
  font-weight: 700;
  font-size: 18px;
}

.earn ul li /deep/ .el-button.el-button--primary {
  background-color: #02bbf0;
  border-color: #02bbf0;
}
.earn .pool-button {
  margin-top: 20px;
  margin-bottom: 10px;
}
.earn .pool-button .pool-tips {
  font-size: 14px;
  margin-top: 10px;
  font-weight: 200;
}

.earn .pool-action {
  margin-top: 10px;
}
.earn .pool-action-button /deep/ .el-button {
  width: 350px;
}

.earn .pool-rate {
  position: absolute;
  right: 0;
  top: 0;
  width: 65px;
  height: 65px;
  font-size: 14px;
  border-radius: 0 40px 0 100%;
  background-color: #02bbf0;
  text-align: center;
  line-height: 55px;
  box-shadow: 0 0 10px #02bbf0;
  color: #fff;
}

.earn .pool-rate p {
  transform: rotate(50deg);
}

.earn .pool-rank {
  position: absolute;
  left: 30px;
  top: 0;
  width: 60px;
  height: 58px;
  border-radius: 0 0 30% 30%;
  background-color: #02bbf0;
  text-align: center;
  line-height: 55px;
  font-size: 14px;
  box-shadow: 0 0 5px #02bbf0;
  color: #fff;
  cursor: pointer;
}
.tips {
  color: #fff;
  text-align: center;
  line-height: 30px;
  padding-top: 30px;
}
.stake-dialog {
  text-align: center;
}
.stake-dialog button {
  width: 80%;
  margin-top: 15px;
  margin-bottom: 10px;
}

.nft-dialog {
  padding-left: 5px;
  padding-right: 5px;
}
.nft-dialog .nft-slider /deep/ .el-slider {
  margin-bottom: 40px;
  padding-left: 15px;
  padding-right: 15px;
}
.nft-dialog /deep/ .el-slider__marks-text {
  width: 50px;
  text-align: center;
}

.nft-dialog /deep/ .el-slider__marks-text span {
  display: block;
}
.nft-dialog .nft-label,
.nft-dialog .nft-input {
  margin-top: 10px;
  display: flex;
}
.nft-dialog .nft-label span {
  flex: 1;
}
.nft-dialog .nft-input .nft-input-item {
  flex: 1;
}

.nft-dialog .nft-button {
  text-align: center;
}
.nft-dialog button {
  width: 80%;
  margin-top: 15px;
  margin-bottom: 10px;
}
.nft-dialog .nft-button button {
  width: 45%;
}

.unstake-dialog .unstake-box {
  margin-top: 16px;
  position: relative;
  background: #fff;
  border-radius: 0.43333rem;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}
.unstake-dialog .unstake-header,
.unstake-dialog .unstake-mark {
  display: flex;
  justify-content: space-between;
}

.unstake-dialog .unstake-header {
  margin-bottom: 20px;
}
.unstake-dialog .market-add-divider {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}
.unstake-dialog .unstake-box /deep/ .el-input__inner {
  padding: 0;
  border: 0;
  border-radius: 0;
  border-bottom: 1px solid #cfcfcf;
  color: #000;
  font-weight: 500;
  height: 50px;
  font-size: 16px;
}
.unstake-dialog .unstake-box /deep/ .el-button {
  margin-top: 10px;
}

.unstake-dialog .unstake-button {
  text-align: center;
  margin-top: 15px;
}

.unstake-dialog .unstake-button button {
