}
/* 适配 */
@media only screen and (max-width: 900px) {
  .banner {
    height: 100px;
  }
  .banner h2 {
    font-size: 22px;
    line-height: 34px;
    margin-bottom: 15px;
  }
  .earn ul {
    flex-wrap: wrap;
  }
  .earn ul li {
    width: 100%;
    margin-bottom: 20px;
    border-radius: 20px;
    padding: 30px 20px;
  }

  .tabMenu .boxMain,
  .tabMenu {
    display: flex;

    justify-content: center;
  }

  .tabMenu .boxMain {
    display: flex;

    justify-content: center;
    width: 100%;
    margin: 0 10px;
  }

  .tabMenu .boxMain a {
    font-size: 14px;
    padding: 0 10px;
  }
  .earn .pool-action {
    display: block;
    text-align: center;
  }
  .earn .pool-action button {
    margin-bottom: 10px;
  }
  .earn .pool-action /deep/ .el-button {
    width: 140px;
  }
  .earn .pool-action-button /deep/ .el-button {
    width: 290px;
  }

  .earn .el-button + .el-button {
    margin-left: 0px;
  }

  .earn .pool-rate {
    border-radius: 0 20px 0 100%;
  }
}
</style>


