<template>
  <div id="app wrapper">
    <router-view />
  </div>
</template>

<script>
import { setup } from "./locales/index";
import Storage from "./utils/storage";

export default {
  name: "App",
  data() {
    return {};
  },
  methods: {},
  beforeCreate() {
    var lang = this.$route.query.lang;
    if (lang == "en-us" || lang == "en") {
      setup("en");
    } else if (lang == "zh-hans" || lang == "zh-hant" || lang == "zh") {
      setup("zh");
    } else {
      setup();
    }
  },
  beforeMount() {
    var debug = this.$route.query.debug;
    console.log(debug,"debug.......")
    if (debug == 1) {
      Storage.set("gamedao_debug", debug);
    } else if(debug == 0) {
      Storage.remove("gamedao_debug");
    }
    var ref = this.$route.query.ref;
    if (ref) {
      this.$store.dispatch("ASET_REF", ref);
    }
  },
  created() {},
  mounted() {}
};
</script>

<style>
* {
  margin: 0;
  padding: 0;
}
.wrapper {
  /* background: url("assets/mainbg.png") no-repeat top; */
}
.fontStyle {
  font-family: Barlow-ExtraBold;
}
html {
  -webkit-tap-highlight-color: transparent;
}
body {
  margin: 0;
  color: #999;
  background-color: #fff;
  font-family: Montserrat, sans-serif, Microsoft YaHei !important;
}
a {
  text-decoration: none;
  transition: all 0.4s cubic-bezier(0.215, 0.61, 0.355, 1);
  cursor: pointer;
}

a:focus,
button:focus,
input:focus,
textarea:focus {
  outline: 0;
}
