  color: inherit;
  font: inherit;
}

html {
  background-color: rgb(74, 73, 73);
  margin: 0;
  padding: 0;
}
body {
  background-color: rgb(251, 249, 246);
  overflow-x: hidden !important;
  margin: 0 auto;
  width: 100%;
  min-width: 200px;
}

.el-button--primary {
  background: #02bbf0;
  border-color: #02bbf0;
}

.el-dialog__title {
  color: #3ebddc;
}

@media only screen and (max-width: 900px) {


  .banner h1 {
    font-size: 34px;
    color: #fff;
    line-height: 40px;
  }

  .banner h2 {
    font-size: 22px;
    line-height: 34px;
    margin-bottom: 15px;
  }

