} */

.el-dialog__wrapper {
  /* width: 100%; */
  margin: 15px !important;
  border-radius: 0.375rem;
}

.el-dialog {
  width: 100%;
}

.el-dialog__body {
  padding: 10px 20px 20px;
}

@media (min-width: 768px) {
  .el-dialog__wrapper {
    max-width: 414px;
    margin: 0 auto !important;
  }
}
@media (min-width: 900px) {
  .el-dialog__wrapper {
    max-width: 600px;
    margin: 0 auto !important;
  }
}
</style>



