<script>
import store from "@/store/index";
import Util from "../../utils/util";
import ServerService from "../../api/server";

var path = store.state.contractPath;

const erc20AbiJosn = require("../../contracts/erc20.json");
const tokensJson = require("../../contracts/tokens." + path + ".json");
const gdbAbiJsons = require("../../contracts/gdbdeployments." + path + ".json");
const gdbPoolJsons = require("../../contracts/gdbpools." + path + ".json");

import Swap from "../../utils/swap";

export default {
  data() {
    return {
      yiiContracts: {},
      mineContracts: {},
      rankContract: null,

      txs: {},

      yiiPools: {},
      minePools: {},

      popularRankDialogVisiable: false,
      popularRankLoading: true,
      rankInfo: {}
    };
  },

  computed: {
    web3() {
      return this.$store.state.web3;
    }
  },
  watch: {
    web3(val1, val2) {
      this.init();
    }
  },
  beforeMount() {
    var yiiPools = {};
    for (var i in gdbPoolJsons["yiis"]) {
      var item = gdbPoolJsons["yiis"][i];
      yiiPools[i] = {
        key: i,
        token1: item.tokenName1,
        token2: item.tokenName2,
        type: item.type,
        tokenName: item.tokenName,
        poolAddress: item.poolAddress,
        poolName: item.poolName,
        total: 0,
        balance: 0,
        apy: "--"
      };
    }
    this.yiiPools = yiiPools;

    var curtime = parseInt(new Date() / 1000);
    var minePools = {};
    for (var i in gdbPoolJsons["mines"]) {
      var item = gdbPoolJsons["mines"][i];
      minePools[i] = {
        key: i,
        poolName: item.poolName,
        poolAddress: item.poolAddress,
        token1: item.tokenName1,
        token2: item.tokenName2,
        type: item.type,
        tokenName: item.tokenName,
        balance: 0,
        apy: "--",
        version: item.version,
        dex: item.dex,
        isOver: item.end_time < curtime ? true : false
      };
    }
    this.minePools = minePools;
  },
  mounted() {
    this.init();

    setInterval(
      function() {
        this.update();
      }.bind(this),
      1000
    );
  },
  methods: {
    update() {},
    showSaveDialog() {
      this.saveDialogVisiable = true;
    },
    init() {
      if (!this.web3 || !this.web3.web3Instance || this.savingContract) {
        return;
      }
      var web3 = this.web3.web3Instance();
      var yiiContracts = {};
      for (var i in this.yiiPools) {
        var key = this.yiiPools[i]["poolName"];
        var address = this.yiiPools[i]["poolAddress"];
        yiiContracts[i] = new web3.eth.Contract(
          gdbAbiJsons[key]["abi"],
          address,
          {
            from: this.web3.coinbase,
            gasPrice: "2000000000"
          }
        );
      }
      this.yiiContracts = yiiContracts;

      var mineContracts = {};
      for (var i in this.minePools) {
        var key = this.minePools[i]["poolName"];
        var address = this.minePools[i]["poolAddress"];
        mineContracts[i] = new web3.eth.Contract(
          gdbAbiJsons[key]["abi"],
          address,
          {
            from: this.web3.coinbase,
            gasPrice: "2000000000"
          }
        );
      }
      this.mineContracts = mineContracts;

      this.rankContract = new web3.eth.Contract(
        gdbAbiJsons["Rank"]["abi"],
        gdbAbiJsons["Rank"]["address"],
        {
          from: this.web3.coinbase,
          gasPrice: "2000000000"
        }
      );

      this.getInfo();
      setTimeout(
        function() {
          this.getRankInfo();
        }.bind(this),
        1000
      );
      return;

      this.gdtContract = new web3.eth.Contract(
        erc20AbiJosn,
        tokensJson["GDT"]["address"],
        {
          from: this.web3.coinbase,
          gasPrice: "2000000000"
        }
      );
    },
    getInfo() {
      this.getYiiInfos();
      this.getMineInfos();
    },
    getYiiInfos() {
      for (var i in this.yiiPools) {
        this.getYiiInfo(i);
      }

      var that = this;
      ServerService.getMdxApy().then(function(res) {
        if (res && res.code == 0) {
          for (var i in res.result) {
            var row = res.result[i];
            // console.log(row);
            var pool_name = row.pool_name.replace("/", "") + "LP";
            var apy = (1 + (row.pool_apy * 0.95) / 100) ** 365 * 100;

            if (
              that.yiiPools[pool_name] &&
              that.yiiPools[pool_name]["type"] == "mdex"
            ) {
              that.yiiPools[pool_name]["apy"] = Number(apy).toFixed(2);

              that.yiiPools[pool_name]["apytips"] =
                "机枪池复利收益:" +
                Number(apy).toFixed(2) +
                "%,MDEX官方收益:" +
                Number(row.pool_apy * 365).toFixed(2) +
                "%";
            }
          }
        }
      });
    },
    getMineInfos() {
      for (var i in this.minePools) {
        this.getMineInfo(i);
      }
    },
    getYiiInfo(index) {
      this.yiiContracts[index].methods
        .totalSupply()
        .call({ from: this.$store.state.contractFrom })
        .then(res => {
          var pool = this.yiiPools[index];
          // pool["total"] = res;
          // Vue.set(this.yiiPools, index, pool);
          this.yiiPools[index]["total"] = res;
          this.yiiContracts[index].methods
            .balanceOf(this.web3.coinbase)
            .call({ from: this.$store.state.contractFrom })
            .then(res => {
              // pool["balance"] = res;
              this.yiiPools[index]["balance"] = res;
              // Vue.set(this.yiiPools, index, pool);
            });
          var that = this;
          var token1 = this.yiiPools[index]["token1"];
          var token2 = this.yiiPools[index]["token2"];

          Swap.getTokenLp(token1, token2, function(price, amount1, amount2) {
            console.log(token1, token2, amount1, amount2, "xxxxxxxx");

            var lp = Math.pow(parseFloat(amount1) * parseFloat(amount2), 0.5);
            if (token1 == "HUSD" && token2 == "USDT") {
              var _totalPoolAmount =
                ((parseFloat(that.yiiPools[index]["total"]) / lp) *
                  parseFloat(amount2) *
                  2) /
                10 ** 13;
              console.log(lp, that.yiiPools[index]["total"], _totalPoolAmount);
              var pool = that.yiiPools[index];
              pool["tvl"] = _totalPoolAmount;
              Vue.set(that.yiiPools, index, pool);
            } else if (token1 == "MDX" && token2 == "USDT") {
              var _totalPoolAmount =
                ((parseFloat(that.yiiPools[index]["total"]) / lp) *
                  parseFloat(amount2) *
                  2) /
                10 ** 18;
              console.log(lp, that.yiiPools[index]["total"], _totalPoolAmount);
              var pool = that.yiiPools[index];
              pool["tvl"] = _totalPoolAmount;
              Vue.set(that.yiiPools, index, pool);
            } else {
              var _totalPoolAmount =
                ((parseFloat(that.yiiPools[index]["total"]) / lp) *
                  parseFloat(amount2) *
                  2) /
                10 ** 18;
              console.log(
                lp,
                amount2,
                amount1,
                that.yiiPools[index]["total"],
                _totalPoolAmount
              );
              Swap.getTokenLp(token2, "USDT", function(
                price,
                amount1,
                amount2
              ) {
                var pool = that.yiiPools[index];
                pool["tvl"] = _totalPoolAmount*price;
                Vue.set(that.yiiPools, index, pool);
              });
            }
          });
        });
    },
    getMineInfo(index) {
      // console.log(index, "getMineInfo", "..........");
      this.mineContracts[index].methods
        .getMineInfo()
        .call({ from: this.$store.state.contractFrom })
        .then(res => {
          this.minePools[index]["balance"] = res[3];

          var perReward = res[5];
          this.calApy(index, res[3], perReward);
        });
    },
    showPopularRankDialog() {
      this.popularRankDialogVisiable = true;
      this.getRankInfo();
    },
    getRankInfo() {
      this.rankContract.methods
        .getUserInfo(this.web3.coinbase)
        .call({ from: this.$store.state.contractFrom })
        .then(res => {
          this.rankInfo = {
            remainTotal: res[0] - res[1],
            releasedTotal: res[1],
            myTotal: res[2],
            myUnclaim: res[2] - res[3]
          };

          this.getPopularRank();
        });
    },
    getPopularRank() {
      ServerService.getPopularRank().then(res => {
        this.popularRankLoading = false;
        var datas = [];
        var t = 0;
        for (var i in res.data.datas) {
          var row = res.data.datas[i];

          t += row.popular;
          datas.push(row);
        }
        // console.log(t, datas[0])
        for (var i in datas) {
          datas[i]["amount"] = Number(
            (2.73972602739726 * datas[i]["popular"]) / t
          ).toFixed(6);
        }
        // row.amount = row.reward
        //   ? Number(
        //       (row.reward * this.rankInfo.remainTotal * 0.01) / 10 ** 8
        //     ).toFixed(4)
        //   : "";

        if (res.code == 0) {
          this.popularRankData = datas;
        }
      });
    },
    claimrank() {
      this.showLoading("领取中");

      var that = this;
      this.rankContract.methods
        .withdraw()
        .send()
        .on("transactionHash", function(hash) {
          console.log(hash);
          that.showLoading("领取确认中");
        })
        .on("receipt", function(receipt) {
          console.log(receipt);
          if (that.txs[receipt.transactionHash]) {
            return;
          }
          that.txs[receipt.transactionHash] = receipt.transactionHash;
          that.getInfo();
          that.getRankInfo();
          that.hideLoading();
          that.$message({
            message: "领取成功",
            type: "success",
            center: true
          });
        })
        .on("confirmation", function(confirmationNumber, receipt) {
          console.log(confirmationNumber);
        })
        .on("error", function(err) {
          console.log(err);
          that.hideLoading();

          var msg = err && err.message ? err.message : "";
          that.$message({
            message: "领取失败" + msg,
            type: "error",
            center: true
          });
        });
    },
    calApy(tokenName, totalPoolAmount, perReward) {
      var that = this;

      Swap.getTokenPrice("GDB", function(price, amount1, amount2) {
        var gdbPrice = price;
        if (tokenName.indexOf("LP") > 0) {
          // that.husdAmount = amount1;
          // that.gdtAmount = amount2;
          if (totalPoolAmount == 0) {
            that.minePools[tokenName].apy = "--";
            return;
          }
          console.log(tokenName);
          if (tokenName == "GDTHUSDLPV2" || tokenName == "GDTHUSDLPV1") {
            Swap.getTokenPrice("GDT", function(price, amount1, amount2) {
              var lp =
                Math.pow(
                  parseFloat(amount1) * parseFloat(amount2) * 10 ** 10,
                  0.5
                ) *
                10 ** 8;

              // var _totalPoolAmount1 =
              //   (parseFloat(totalPoolAmount) / lp) * parseFloat(amount1); //totalPoolAmount / 10 ** 13;

              var _totalPoolAmount = Math.pow(
                (totalPoolAmount / 10 ** 13) ** 2 * price,
                0.5
              );

              that.minePools[tokenName].apy = Number(
                ((perReward / 10 ** 18) * 86400 * 365 * 100 * gdbPrice) /
                  _totalPoolAmount /
                  2
              ).toFixed(2);
            });
          } else if (tokenName == "GDBHUSDLP") {
            var lp =
              Math.pow(
                parseFloat(amount1) * parseFloat(amount2) * 10 ** 10,
                0.5
              ) *
              10 ** 8;
            var _totalPoolAmount =
              (parseFloat(totalPoolAmount) / lp) * parseFloat(amount1); //totalPoolAmount / 10 ** 13;

            // console.log(gdbPrice, "......price", totalPoolAmount);
            that.minePools[tokenName].apy = Number(
              ((perReward / 10 ** 18) * 86400 * 365 * 100 * gdbPrice) /
                _totalPoolAmount /
                2
            ).toFixed(2);
          } else if (tokenName == "GMDXUSDTLP") {
            Swap.getTokenLp("MDX", "USDT", function(price, amount1, amount2) {
              var lp =
                Math.pow(parseFloat(amount1) * parseFloat(amount2), 0.5) *
                10 ** 18;
              // console.log(amount1,amount2)
              var _totalPoolAmount =
                (parseFloat(totalPoolAmount) / lp) * parseFloat(amount2); //totalPoolAmount / 10 ** 13;

              that.minePools[tokenName].apy = Number(
                ((perReward / 10 ** 18) * 86400 * 365 * 100 * gdbPrice) /
                  _totalPoolAmount /
                  2
              ).toFixed(2);

              ServerService.getMdxApy().then(function(res) {
                if (res && res.code == 0) {
                  for (var i in res.result) {
                    var row = res.result[i]; //JSON.parse(res.result.pool_apy[i]);
                    if (row.pool_name == "MDX/USDT") {
                      var apy = parseFloat(row.pool_apy);
                      apy = (1 + (apy / 100) * 0.4) ** 365 * 100;
                      var _apy = parseFloat(that.minePools[tokenName].apy);
                      that.yiiPools[tokenName].apy = Number(apy + _apy).toFixed(
                        2
                      );
                      that.yiiPools[tokenName].apytips =
                        "机枪池复利收益:" +
                        Number(apy).toFixed(2) +
                        "% 抵押挖矿收益:" +
                        Number(_apy).toFixed(2) +
                        "%";
                    }
                    // console.log(row, apy, _apy, "........///////");
                  }
                }
              });
            });
          } else if (tokenName == "GDTUSDTLP") {
            Swap.getTokenPrice("GDT", function(price, amount1, amount2) {
              // var lp =
              //   Math.pow(
              //     parseFloat(amount1) * parseFloat(amount2) * 10 ** 10,
              //     0.5
              //   ) *
              //   10 ** 8;

              var _totalPoolAmount = Math.pow(
                (totalPoolAmount / 10 ** 18) ** 2 * price,
                0.5
              );
              // (parseFloat(totalPoolAmount) / lp) * parseFloat(amount1); //totalPoolAmount / 10 ** 13;

              that.minePools[tokenName].apy = Number(
                ((perReward / 10 ** 18) * 86400 * 365 * 100 * gdbPrice) /
                  _totalPoolAmount /
                  2
              ).toFixed(2);
            });
          }
          return;
        } else {
          Swap.getTokenPrice(tokenName, function(price) {
            var tokenPrice = price;
            if (totalPoolAmount == 0) {
              return;
            }
            var _totalPoolAmount = 1;
            _totalPoolAmount =
              totalPoolAmount / 10 ** that.singleTokens[tokenName]["decimals"];

            var apy = Number(
              ((((perReward / 10 ** 18) * 86400 * 365 * 100) / tokenPrice) *
                2 *
                gdbPrice) /
                _totalPoolAmount
            ).toFixed(2);
            // console.log(perReward, gdbPrice, _totalPoolAmount);
            that.minePools[tokenName]["apy"] = apy;
          });
        }
      });
    },
    selectPool(pool) {
      // this.$emit("func", "pool");
      this.$router.push({ name: "gdbPoolScene", params: { pool: pool } });
    },
    toStake(pool) {
      this.$router.push({ name: "gdbVaultScene", params: { pool: pool } });
    },
    hideLoading() {
      if (this.loading) {
        this.loading.close();
        this.loading = null;
        return;
      }
    },
    showLoading(text) {
      if (this.loading) {
        this.loading.text = text;
        return;
      }

      this.loading = this.$loading({
        lock: true,
        text: text,
        spinner: "el-icon-loading",
        background: "rgba(0, 0, 0, 0.7)"
      });
    }
  },
  filters: {
    filterNumber(value) {
      if (!value) {
        return value;
      }
      return Number(value).toFixed(2);
    },

    filterTokenNumber(value, decimals, num) {
      if (!value) {
        return "0.00";
      }
      return Util.toNumber(value, decimals, num);
    },
    filterTime(value) {
      if (!value) return "--";
      return Util.formatDate(value, "MM-dd hh:mm");
    }
  }
};
</script>

