import Storage from '../utils/storage'

var getAccount = function () {
   var account = Storage.get("game_account")
   if (account && account.name) {
      return account
   }
   return null;
}
var getRef = function () {
   var ref = Storage.get("gamedao_ref")
   if (ref) {
      return ref
   }
   return '';
}
var getContractPath = function () {
   var debug = Storage.get("gamedao_debug")
   if (debug == 1) {
      // return "testnet"
   }
   return process.env.CONTRACT_PATH;
}
var getContractFrom = function () {
   var debug = Storage.get("gamedao_debug")
   if (debug == 1) {
      // return "0x8ADA8d2F58d1BFB4D4AF45D521784F334f749cC2"
   }
   return process.env.CALL_FROM;
}
var getEnv = function () {
   var debug = Storage.get("gamedao_debug")
   if (debug == 1) {
      // return "testnet"
   }
   return process.env.NODE_ENV;
}
var getDebug = function () {
   var debug = Storage.get("gamedao_debug")
   return debug ? debug : 0;
}

let state = {
   account: getAccount(),
   ref: getRef(),
   contractPath: getContractPath(),
   contractFrom: getContractFrom(),
   env: getEnv(),
   debug: getDebug(),
   web3: {
      isInjected: false,
      web3Instance: null,
      networkId: null,
      coinbase: null,
      balance: null,
      error: null,
      from: null
   },
   contractInstance: null

   //  tokens: getTokens(),
}

export default state;


