import getWeb3 from '../utils/getWeb3'

const ASET_SOURCE = 'ASET_SOURCE'
const ASET_ACCOUNT = 'ASET_ACCOUNT'
const ASET_REF = 'ASET_REF'

const actions = {
    [ASET_SOURCE](context, source) {
        context.commit('SET_SOURCE', source);
    },

    [ASET_ACCOUNT](context, account) {
        context.commit('SET_ACCOUNT', account);
    },

    [ASET_REF](context, ref) {
        context.commit('SET_REF', ref);
    },

    registerWeb3({
        commit
    }) {
        console.log('registerWeb3 Action being executed')
        getWeb3.then(result => {
            console.log('committing result to registerWeb3Instance mutation')
            commit('registerWeb3Instance', result)
        }).catch(e => {
            console.log('error in action registerWeb3', e)
        })
    }
}
export default actions;


