<script>
import i18n, { setup } from "../locales/index";

import Util from "../utils/util";
import store from "../store/index";
var path = store.state.contractPath;

const tokensJsons = require("../contracts/tokens." + path + ".json");
const erc20AbiJosn = require("../contracts/erc20.json");
import Storage from "../utils/storage";

export default {
  data() {
    return {
      noticeDialogVisible: false,

      txs: {},

      chainName: "(请登录HECO)",
      gdtBalance: 0,

      gdtTokenAddress: tokensJsons["GDT"]["address"],
      active: "home",
      currentBodyView: "pages",
      wechateDialogVisible: false,
      mynftDialogVisible: false,

      navs: [
        {
          value: "mine",
          label: "挖矿"
        }
      ],
      nav: ""
    };
  },
  computed: {
    web3() {
      return this.$store.state.web3;
    }
  },

  watch: {
    $route: {
      handler: function(route) {
        var route_name = this.$route.name;
        var path = this.$route.path;
        console.log(this.active, ".///////");

        if (path.indexOf("/wallet") == 0) {
          this.active = "wallet";
        } else if (path.indexOf("/pool") == 0) {
          this.active = "mine";
        } else if (path.indexOf("/mine") == 0) {
          this.active = "mine";
        } else if (path.indexOf("/market") == 0) {
          this.active = "market";
        } else if (path.indexOf("/gss") == 0) {
          this.active = "gss";
        } else if (path.indexOf("/gdb") == 0) {
          this.active = "gdb";
        } else if (path.indexOf("/pixel/graffiti") == 0) {
          this.active = "graffiti";
        } else if (path.indexOf("/pixel") == 0) {
          this.active = "pixel";
        } else if (path.indexOf("/shop") == 0) {
          this.active = "shop";
        } else {
          this.active = "home";
        }
      },
      immediate: true
    },
    web3(val1, val2) {
      this.init();
    },
    active(val1, val2) {
      console.log(val1, val2);
    }
  },
  beforeMount() {
    this.hideLoading();
    this.$store.dispatch("registerWeb3");
    var route_name = this.$route.name;
  },
  mounted() {
    var debug = Storage.get("gamedao_debug");
    this.debug = debug ? true : false;
    var ret = Util.getCardInfo(
      "34028236692093846376097748942960328055661265646"
    );
    var key = Storage.get("gamedao_notice");
    if (key != "014") {
      Storage.set("gamedao_notice", "014");
      this.noticeDialogVisible = true;
    }
  },
  beforeDestroy() {
    this.hideLoading();
  },
  methods: {
    hideLoading() {
      if (this.loading) {
        this.loading.close();
        this.loading = null;
        return;
      }
    },
    showLoading(text) {
      if (this.loading) {
        this.loading.text = text;
        return;
      }

      this.loading = this.$loading({
        lock: true,
        text: text,
        spinner: "el-icon-loading",
        background: "rgba(0, 0, 0, 0.7)"
      });
    },
    setActive() {
      var path = this.$route.path;
      if (path.indexOf("/about") == 0) {
        this.active = "about";
      } else if (path.indexOf("/mine") == 0) {
        this.active = "mine";
      } else {
        this.active = "index";
      }
    },
    settingChange(visible) {
      console.log("visible", visible);
      if (visible) {
        this.active = "setting";
      } else {
        this.setActive();
      }
    },
    navCommand(command) {
      console.log(command);
      if (command == "about") {
        this.toAbout();
      } else {
        this.toNav(command);
      }
      // this.setLang(command);
      return false;
    },
    toAbout() {
      window.location.href = "https://bihu.com/article/1166598656";
    },
    toNav(name) {
      console.log(name + "Scene");
      this.$router.push({
        name: name + "Scene"
      });
    },
    login() {},
    changeBodyView(view) {
      this.currentBodyView = view;
    },

    showMyNft() {
      this.mynftDialogVisible = true;
      this.getMyInfo();
    },
    init() {
      var chainId = this.web3.networkId;

      if (chainId == 128) {
        this.chainName = "(HECO主网)";
      } else if (chainId == 256) {
        this.chainName = "(HECO测试网网)";
      }
      var web3 = this.web3.web3Instance();

      this.gdtContract = new web3.eth.Contract(
        erc20AbiJosn,
        this.gdtTokenAddress,
        {
          from: this.web3.coinbase,
          gasPrice: "2000000000"
          // gas: 77026
        }
      );

      // this.getMyInfo();
    },
    getMyInfo() {
      this.getGdtBalance();
    },
    getGdtBalance() {
      this.gdtContract.methods
        .balanceOf(this.web3.coinbase)
        .call({ from: this.$store.state.contractFrom })
        .then(res => {
          this.gdtBalance = res;
          console.log("getGdtBalance balanceOf", res);
        });
    }
  },

  filters: {
    filterAccount(value) {
      if (!value) {
        return value;
      }
      return value.slice(0, 4) + "***" + value.slice(-3);
    },

    filterTokenAmount(value) {
      return Number(value / 1).toFixed(2);
    },
    filterBalance(value) {
      return Number(value / 10 ** 18).toFixed(6);
    }
  }
};
</script>

