import Vue from "vue";
import Router from "vue-router";
import store from "@/store/index";
// 基础页面
import index from "@/components/index.vue";

import VueTitle from "vue-wechat-title";
Vue.use(VueTitle);

// DeFi
import homeScene from "@/components/home/home";
import gdbScene from "@/components/gdb/gdb";
import gdbPoolScene from "@/components/gdb/pool";
import gdbVaultScene from "@/components/gdb/vault";

Vue.use(Router);

var routes = [];
routes = [{
  // 基础界面
  path: "/",
  name: "index",
  component: index,
  meta: {
    title: "GameDao",
  },
  children: [{
      path: "/",
      name: "homeScene",
      component: homeScene,
    },
    {
      path: "/gdb",
      name: "gdbScene",
      component: gdbScene,
    },
    {
      path: "/gdb/pool/:pool",
      name: "gdbPoolScene",
      component: gdbPoolScene,
    },
    {
      path: "/gdb/vault/:pool",
      name: "gdbVaultScene",
      component: gdbVaultScene,
    },
  ],
}, ];

var router = new Router({
  routes: routes,
  mode: "history",
  srcollBehavior(to, from, savedPosition) {
    if (to.hash) {
      return {
        selector: to.hash,
      };
    }
  },
});

router.beforeEach((to, from, next) => {
  next();
});
export default router;


