export default {


    randomString(len) {
        len = len || 32;
        var $chars = "abcdefghijklmnopqrstuvwxyz0123456789";
        var maxPos = $chars.length;
        var pwd = "";
        for (var i = 0; i < len; i++) {
            pwd += $chars.charAt(Math.floor(Math.random() * maxPos));
        }
        return pwd;
    },

    formatDate: function formatDate(t, fmt) {
        var d = new Date(1000 * parseInt(t))
        var o = {
            "M+": d.getMonth() + 1, //月份 
            "d+": d.getDate(), //日 
            "h+": d.getHours(), //小时 
            "m+": d.getMinutes(), //分 
            "s+": d.getSeconds(), //秒 
            "q+": Math.floor((d.getMonth() + 3) / 3), //季度 
            "S": d.getMilliseconds() //毫秒 
        };
        if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (d.getFullYear() + "").substr(4 - RegExp.$1.length));
        for (var k in o) {
            if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, RegExp.$1.length == 1 ? o[k] : ("00" + o[k]).substr(("" + o[k]).length));
        }
        return fmt;
    },

    isMobile: function () {
        return (navigator.userAgent.match(/AppleWebKit.*Mobile.*/) ||
            !!navigator.userAgent.match(/iOS38*/) ||
            navigator.userAgent.match(/iPhone/) ||
            navigator.userAgent.match(/Android/)
        ) ? true : false
    },
    getIntegerBits: function (t, e, r) {
        var n = t.shiftRight(e).and(BigInteger.ONE.shiftLeft(r).subtract(BigInteger.ONE));
        return Number(n.toString())
    },
    getTokenDecimals(symbol) {
        var symbols = {
            GDT: 18,
            HUSD: 18,
        }
        return symbols[symbol] ? symbols[symbol] : 18
    },
    toNumber: function (amount, decimals, number) {
        number = decimals > number ? number : decimals;
        // var decimals = this.getTokenDecimals(symbol);
        var t = new BigInteger(amount.toString());
        var n = t.divide(new BigInteger(10 ** (decimals - number) + ""));
        return Number(parseFloat(n.toString()) / 10 ** number).toFixed(number);
    },

    toBigInteger: function (t) {
        return t = t.toString().toLowerCase(),
            this.stringStartsWith(t, "0x") ? new BigInteger(t.substring(2), 16) : new BigInteger(t)
    },
    stringStartsWith: function (t, e) {
        return t.substring(0, e.length) == e
    },

    getCardInfo: function (cardid) {
        cardid = cardid.toString().toLowerCase()
        // console.log(cardid)
        var t = new BigInteger(cardid);

        // nftSign  cardType    skin    rarity tokenAmount  padding mintTime    index
        // 1        31          16      16     64           24      40          64
        // 255      224         208     192    128          104     64          0

        var mintTime = this.getIntegerBits(t, 64, 40);
        return {
            id: t.toString(),
            type: this.getIntegerBits(t, 224, 31),
            skin: this.getIntegerBits(t, 208, 16),
            rarity: this.getIntegerBits(t, 192, 16),
            tokenAmount: this.getIntegerBits(t, 128, 64) / 10 ** 8,
            padding: this.getIntegerBits(t, 104, 24),
            mintTime: mintTime,
            burned: (parseInt(new Date() / 1000) - mintTime) > 86400 * 2 ? true : false,
            index: this.getIntegerBits(t, 0, 64)
        };
    },

    getPixelInfo: function (id) {
        id = id.toString().toLowerCase()
        // console.log(cardid)
        var t = new BigInteger(id);

        // nftSign  nftType  md5   version size   xxx      mintTime    index
        // 1        15       128    8      16     24       40          48
        // 255      240      112   104     88    104      48          0

        var mintTime = this.getIntegerBits(t, 48, 40);
        return {
            id: t.toString(),
            md5: this.getIntegerBits(t, 112, 128).toString(16),
            type: this.getIntegerBits(t, 240, 15),
            version: this.getIntegerBits(t, 104, 8),
            size: this.getIntegerBits(t, 88, 16),
            mintTime: mintTime,
            index: this.getIntegerBits(t, 0, 48)
        };
    },
    getPackageInfo: function (cardid) {
        cardid = cardid.toString().toLowerCase()
        var t = new BigInteger(cardid);

        // nftSign  packageType tokenAmount quantity    padding mintTime    index
        // 1        31          64          16          40      40          64
        // 255      224         160         144         104     64          0

        return {
            id: t.toString(),
            type: this.getIntegerBits(t, 224, 31),
            tokenAmount: this.getIntegerBits(t, 160, 64) / 10 ** 8,
            quantity: this.getIntegerBits(t, 144, 16),
        };
    },
    add(t1, t2) {
        var t = new BigInteger(t1.toString());
        return t.add(new BigInteger(t2.toString()));
    },

    mul(t1, t2) {
        var t = new BigInteger(t1.toString());
        return t.multiply(new BigInteger(t2.toString()));
    },

    div(t1, t2) {
        console.log(t1, t2)
        var t = new BigInteger(t1.toString());
        return t.divide(new BigInteger(t2.toString()));
    },

    compareTo(t1, t2) {
        var t = new BigInteger(t1.toString());
        return t.compareTo(new BigInteger(t2.toString()));
    },

    toTypeName: function (type) {
        var nfts = {
            1: "魂斗罗",
            2: "加密猫",
            6: "加密猫"
        }

        return nfts[type]
    },

    countdownHour: function (t) {
        var str =
            parseInt(t / 3600) > 9 ? parseInt(t / 3600) : "0" + parseInt(t / 3600);
        str += ":"
        t = t % 3600
        str +=
            parseInt(t / 60) > 9 ? parseInt(t / 60) : "0" + parseInt(t / 60);
        str += ':'
        str +=
            parseInt(t % 60) > 9 ? parseInt(t % 60) : "0" + parseInt(t % 60);

        return str;
    }

}


