import {
    ChainId,
    Token,
    Fetcher,
    Route
} from 'gamedao'
var ethers = require('ethers');

var tokensJson = require("../contracts/tokens.mainnet.json");

var provider = null;

export default {

    web3ProviderFrom(endpoint, config) {
        var defaultEthereumConfig = {
            testing: false,
            autoGasMultiplier: 1.5,
            defaultConfirmations: 1,
            defaultGas: "6000000",
            defaultGasPrice: "3000000000",
            ethereumNodeTimeout: 10000,
        };
        var ethConfig = Object.assign(defaultEthereumConfig, config || {})

        var providerClass = endpoint.includes('wss') ?
            Web3.providers.WebsocketProvider :
            Web3.providers.HttpProvider

        return new providerClass(endpoint, {
            timeout: ethConfig.ethereumNodeTimeout,
        })
    },

    getTokenPrice(token, cb) {
        token = token.indexOf("-") > 0 ? token.slice(0, token.indexOf("-")) : token;
        // console.log(token)
        if (token == "HUSD") {
            return cb(1)
        }

        var token1 = tokensJson[token]
        if (token == "HT") {
            token1.address = "0x5545153ccfca01fbd7dd11c0b23ba694d9509a6f"
        }
        var token2 = tokensJson["HUSD"]

        this.getPrice(token1, token2, cb)
    },

    getTokenLp(token1, token2, cb) {
        // token = token.indexOf("-") > 0 ? token.slice(0, token.indexOf("-")) : token;
        var _token1 = tokensJson[token1]
        if (token1 == "HT") {
            token1.address = "0x5545153ccfca01fbd7dd11c0b23ba694d9509a6f"
        }
        var _token2 = tokensJson[token2]
        this.getPrice(_token1, _token2, cb)
    },

    getTokenAmount(token, cb) {
        if (token == "HUSD") {
            return cb(1)
        }
        var token1 = tokensJson[token]
        var token2 = tokensJson["HUSD"]
        this.getAmount(token1, token2, cb)
    },

    getPrice(token1, token2, cb) {
        if (provider == null) {
            provider = new ethers.providers.Web3Provider(
                this.web3ProviderFrom('https://http-mainnet-node.huobichain.com'),
                ChainId.HEM,
            )
        }

        var _token1 = new Token(ChainId.HEM, token1.address, token1.decimals)
        var _token2 = new Token(ChainId.HEM, token2.address, token2.decimals)

        // note that you may want/need to handle this async code differently,
        // for example if top-level await is not an option
        Fetcher.fetchPairData(_token1, _token2, provider).then(pair => {
            var route = new Route([pair], _token2)
            var midPrice = route.midPrice;
            var price = midPrice.invert().toSignificant(6)
            // console.log(route, pair.tokenAmounts[0].toSignificant(18), pair.tokenAmounts[1].toSignificant(18))
            cb(price, pair.tokenAmounts[0].toSignificant(18), pair.tokenAmounts[1].toSignificant(18))
        })
    },

    getAmount(token1, token2, cb) {
        var provider = new ethers.providers.Web3Provider(
            this.web3ProviderFrom('https://http-mainnet-node.huobichain.com'),
            ChainId.HEM,
        )

        var _token1 = new Token(ChainId.HEM, token1.address, token1.decimals)
        var _token2 = new Token(ChainId.HEM, token2.address, token2.decimals)

        Fetcher.fetchPairData(_token1, _token2, provider).then(pair => {
            cb(pair.tokenAmounts[0].toSignificant(18), pair.tokenAmounts[1].toSignificant(18))

        })
    }


}


