import Http from './http'

export default {

    getPopularRank() {
        return Http.get({
            path: "/popular/rank",
            headers: {},
            data: {}
        }).then(function (response) {
            return response
        }).catch(function (error) {})
    },

    getApyRank() {
        return Http.get({
            path: "/apy/rank",
            headers: {},
            data: {}
        }).then(function (response) {
            return response
        }).catch(function (error) {})
    },

    getCardRank() {
        return Http.get({
            path: "/card/rank",
            headers: {},
            data: {}
        }).then(function (response) {
            return response
        }).catch(function (error) {})
    },

    uploadImg(id, title, md5str, img) {
        return Http.post({
            path: "/pixel/upload",
            headers: {},
            data: {
                md5str: md5str,
                id: id,
                title: title,
                img: img
            }
        }).then(function (response) {
            return response
        }).catch(function (error) {})
    },


    // url https://gateway.mdex.cc/v2/mingpool/lps?mdex_chainid=128
    // url https://api.mdex.com/boardroom/apys
    getMdxApy() {
        return Http.get({
            url: "https://gateway.mdex.cc/v2/mingpool/lps?mdex_chainid=128",
            path: "",
            headers: {},
            data: {}
        }).then(function (response) {
            return response
        }).catch(function (error) {})
    }
}


